.nav a {
    color: black;
    font-weight: bolder;
    text-shadow: 1px 1px 2px grey;
    transition: text-shadow 0.2s ease-in-out, color 0.5s;
    box-sizing: revert;
}

.nav a:hover {
    border-top: 2px solid black;
    color: ivory;
    text-shadow: 1px 1px 2px black;
}

.lines div{
    border-top: 3px solid black;
    margin-bottom: 2px;
    width: 25px;
}

@media (max-width: 800px) {
    .nav a:hover {
        border-top: none;
    }
}